
<style lang="less" scoped>
  .no-data-img {
    text-align: center;
    transform: scale(0.6);
    &>span {
      display: block;
      color: #777;
      font-size: 40px;
    }
  }
  .live_item {
    height: 100%;
    width: 100%;
    margin-top: .5625rem;
  }
  .title {
    display: block;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    font-size: 30px;
  }
  .course_text_detail_pad {
    padding: 5px 0;
    vertical-align: middle;
    .course_text_detail_pad_img {
      padding-right: 20px;
      vertical-align: middle;
    }
  }
  .course_text_detail>div:nth-child(2) {
    padding: 20px 0 0 0;
  }
  .collection {
    display: block;
    width: 1.25rem;
    height: 1.25rem;
    background-repeat: no-repeat;
    background-size: contain;
  }
  .content_bottom {
    width: 90%;
    height: 110%;
    border-radius: .625rem;
    box-shadow: 0px 2px 15px #ddd;
    margin: 1.25rem auto 0 auto;
  }
  .content_bottom_one {
    margin-top: 0 !important;
  }
  /* 精品课上半部分 START */
  .middel_img_header {
    width: 100%;
    border-radius: .625rem .625rem 0 0;
    overflow: hidden;
  }
  .middel_img_header_text {
    position: absolute;
  }
  .middel_img_header_right_img {
    width: 100%;
    height: 100%;
    img {
      width: 100%;
      height: 100%;
    }
  }
  /* 精品课上半部分 END */
  /* 精品课下半部分 START */
  .middle_title {
    text-indent: 1em;
  }
  .middle_img_footer {
    display: flex;
  }
  .middle_img_footer .middle_detail {
    -webkit-box-flex: 8;
    flex: 8;
    padding: .3125rem 0 .3125rem 0;
    text-indent: 1em;
  }
  .middle_attention {
    text-indent: 2em;
    img {
      width: 1.5625rem;
    }
  }
  .middle_attention>button {
    border: .0625rem solid #ccc;
    outline: none;
    background-color: transparent;
    border-radius: .625rem;
    width: 3.125rem;
    height: 1.875rem;
  }
  .middle_img_footer .middle_detail>div {
    font-size: 1rem;
    margin-bottom: .0625rem;
  }
  .middle_img_footer .middle_detail>div>span {
    color: #777;
  }
  .middle_img_footer .middle_attention {
    -webkit-box-flex: 3;
    flex: 3;
  }
  .middle_img_footer .middle_attention>a>img {
    margin-top: 20%;
    padding-right: 10%;
    width: 80%;
  }
  /* 精品课下半部分 END */
  /* 课程列表 START */
  .course_list {
    padding-top: .3125rem;
  }
  .course_list>.course_flex {
    justify-content: space-between;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
  }
  .flex_middle {
    width: 46%;
    background-color: #fff;
    margin: 10px 0;
    border-radius: 0.266667rem 0.266667rem 0 0;
    overflow: hidden;
  }
  .flex_middle:nth-child(odd){
    margin-left: 20px;
  }
  .flex_middle:nth-child(even){
    margin-right: 20px;
  }
  .flex_middle>div:nth-child(2) {
    padding-left: 20px;
  }
  .course_list .course_flex>div>.course_header_img {
    border-radius: 20px  20px 0 0;
    overflow: hidden;
  }
  .course_list .course_flex>div>.course_header_img>img {
    width: 100%;
    height: 220px;
    border-radius:  20px  20px 0 0;
  }
  .course_text_detail {
    display: -webkit-flex;
    display: flex;
    margin-top: 10px;
  }
  .course_text_detail>div>p {
    font-size: 30px;
    color: #777;
  }
  .course_text_detail>div:nth-child(1) {
    -webkit-box-flex: 7;
    flex: 7;
    padding-left: 20px;
  }
  .course_text_detail>div:nth-child(2) {
    -webkit-box-flex: 3;
    flex: 3;
    padding-right: 20px;
  }
  .course_text_detail>div:nth-child(2)>button {
    background-color: transparent;
    border: .0625rem solid #555;
    border-radius: 20px;
    width: 3.125rem;
    height: 1.875rem;
    color: #555;
  }
  /* 课程列表 END */
  .live-img{
    vertical-align: middle;
    margin-right: 10px;
    width: 30px;
  }
</style>

<template>
  <!-- 课程列表 START -->
  <section class="course_list">
    <div v-if="favorite.length == 0" class="no-data-img">
      <img :src="noDatalist" alt="没有作品哦~">
      <span>暂时没有内容,宝宝先看看别的吧~</span>
    </div>
    <div class="course_flex">
      <!-- 第一个 -->
      <div class="flex_middle" @click="course_list(item)" v-for="(item, index) in favorite" :key="index">
        <div class="course_header_img"><img :src="item.coverImage ? item.coverImage : defaultImg" :alt="item.keywords" :title="item.keywords"></div>
        <div>
          <div class="title">{{item.title}}</div>
        </div>
        <div class="course_text_detail">
          <div>
            <p><img class="live-img" :src="item.liveStatus == 'LIVING' ? livingImg : noLiveImg" alt="播放">{{item.liveStatus == "LIVING" ? "正在直播" : "暂无直播"}}</p>
            <p class="course_text_detail_pad"><img src="../../../static/images/people1.png" width="20px" class="course_text_detail_pad_img">{{item.totalStudent | numFm}}</p>
          </div>
          <div class="coll" v-if="0">
            <img style="width:20px;" :src="item.isFollowed == 'Y' ? flowed : collection">
          </div>
        </div>
      </div>
      <!-- 第一个 -->
    </div>
    <login v-if="showLoginModal && !LOGIN_USER_OBJECT" v-on:showDialogEvent="showDialogEvent"></login>
  </section>
</template>
<script>
  import login from '@/components/login'
  import { createNamespacedHelpers } from 'vuex'
  const { mapMutations, mapActions, mapState} = createNamespacedHelpers('workRangeModule')
  export default {
    components: {
      login
    },
    data() {
      return {
        favorite: [],
        collection: require('../../assets/img/heart_g.png'),
        flowed: require('../../assets/img/heart_r.png'),
        falgNoData: false,
        page: 1,
        pagination: true,
        showData: false,
        touchArr: new Array(),
        noDatalist: require('../../assets/img/da.png'),
        showLoginModal: 0,
        defaultImg: require('../../assets/img/course-large.png'),
        livingImg: require('../../assets/img/open.gif'),
        noLiveImg: require('../../assets/img/hf.png')
      }
    },
    mounted () {
      setTimeout(() => {
        window.onscroll = () => {
          this.dropDown()
        }
      }, 1000)
    },
    created() {
      this.$Indicator.open({
        text: '加载中...',
        spinnerType: 'fading-circle'
      })
      this.axios.get('/api/displaywindow/favorite', {
        params: {
          userId: this.$route.params.id,
          page: this.page
        }
      }).then(data => {
        if(data.data.list && data.data.list.length > 0){
          data.data.forEach(element => {
            if (element.coverImage && element.coverImage.indexOf('http') == -1) {
              element.coverImage = this.productURL + element.coverImage
            }
          })
        }
        this.favorite = data.data
        this.$Indicator.close()
      }).catch(err => {
        console.log(err);
      })
    },
    computed: {
      LOGIN_USER_OBJECT(){
        return this.$store.state.userModule.LOGIN_USER_OBJECT
      }
    },
    methods: {
      // 隐藏登录Modal
      showDialogEvent(data) {
        this.showLoginModal = data
      },
      // 添加和移除收藏
      favorite_add(item) {
        if (item.isFollowed == 'N') {
          this.axios.post('/api/favorite', {
            courseId: item.courseId
          }).then(data => {
            if (data.data == '添加成功') {
              this.$set(item, 'isFollowed', 'Y')
              this.$Toast({
                message: '添加成功',
                position: 'middle',
                duration: 1000
              })
            }
          }).catch(err => {
            console.log(err)
          });
        } else {
          this.axios.post('/api/favorite/delete', {
            courseId: item.courseId
          }).then(data => {
            if (data.data == '移除成功') {
              this.$set(item, 'isFollowed', 'N')
              this.$Toast({
                message: '移除成功',
                position: 'middle',
                duration: 1000
              })
            }
          }).catch(err => {
            console.log(err);
          });
        }
      },
      // 查询课程信息
      course_list(info) {
        if (!this.LOGIN_USER_OBJECT && info.courseType !== 'VIP') {
          this.loginModalStatus = true;
          return
        }
        if(info.courseType === 'VOD'){
          // 正在直播并已登录情况下可进入
          if (info.liveStatus == "LIVING") {
            this.$router.push({
              name: "live",
              params: {
                id: info.lastCurriculum.liveChannelId,
                courseId: info.courseId
              }
            })
          } else {
            this.$Toast({
              message: "暂无直播",
              position: "middle",
              duration: 1000
            });
          }
        }else if(info.courseType === 'VIP'){
          this.$router.push({
            name: 'vipDetail',
            params: {
              id: info.courseId
            }
          })
        }
      },
      dropDown() {
        if (this.falgNoData) { //数据全部加载完了
          return
        } else if (this.scrollEvent() && this.pagination) { //如果满足触发条件，执行
          this.$Indicator.open({
            text: '加载中...',
            spinnerType: 'fading-circle'
          })
          ++this.page
          this.axios.get('/api/displaywindow/favorite', {
            params: {
              userId: this.$route.params.id,
              page: this.page
            }
          }).then((res) => {
            if (res.data && res.data.length > 0) {
              this.favorite = this.favorite.concat(res.data)
              this.$Indicator.close()
            } else {
              this.falgNoData = false
              this.$Indicator.close()
              this.$Toast({
                message: '没有更多了~',
                position: 'middle',
                duration: 1000
              })
            }
          }).catch((err) => {
            console.log(err)
          })
        }
      }
    }
  }
</script>


